@use '../../style/variables.scss' as v;

.home_content {
    // background: #38618C;
    // background: #2A435E;
    // background-image: url("../../assets/images/earth1.PNG");
    // background-position: right;
    // background-size: cover;
    // background-size: 130%;
    color: #fff;
    width: 100vw;
    position: absolute;
    top: v.$header_size;
    left: 0;
    height: calc( 100vh - v.$header_size );
    overflow-y: scroll;
    overflow-x: hidden;
    z-index: 1;
    
    .img_background {
        position:fixed;
        top: v.$header_size;
        left: 0;
        z-index: 0;
        background-image: url("../../assets/images/earth1.PNG");
        background-position: right;
        background-size: cover;
        // filter: blur(4px);
        width: 100vw;
        height: 100vh;
        overflow: hidden;

        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            backdrop-filter: blur(1px);
            pointer-events: none; 
          }

    }

    .content {

        position: relative;
        z-index: 1;
        // width: 100%;
        // padding: calc( ( 100vw - v.$content_width ) / 2);
        // overflow-x: hidden;
        // height: calc( 100vh - v.$header_size );
        background-color: #00000000;
        padding-bottom: 50px;

    }

    .img_logo_container{
        background-color: rgb(255, 255, 255);
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 20vh auto;
        width: 60vh;
        height: 60vh;

        .img_logo {
            // width: 500px;
            height: 55vh;
            object-fit: contain;
        }

        .img_logo_enac {
            position: absolute;
            background-color: rgb(255, 255, 255);
            // width: 500px;
            bottom: 0;
            right: 0;
            height: 8vh;
            object-fit: contain;
        }
        
        .img_logo_dsna {
            position: absolute;
            bottom: 0;
            left: 0;
            background-color: rgb(255, 255, 255);
            // width: 500px;
            height: 8vh;
            object-fit: contain;
        }
    }

    .img_with_text_container {

        position: relative;
        // margin-bottom: 50px;

        &.img_2 {

            margin-top: -200px;

            img {
                margin-left: calc( 100% - 600px );
            }

            .img_text {
                top: 320px;
            }

        }

        img {
            height: 500px;
            width: 600px;
            object-fit: cover;
            box-shadow: -10px -10px rgba(255, 255, 255, 0.25);

        }

        .img_text {

            position: absolute;
            top: 100px;
            width: 800px;
            color: #fff;
            font-size: 28px;
            background-color: rgba(0, 0, 0, 0.4);
            backdrop-filter: blur(50px);
            padding: 20px;

            &.right {
                left: 550px;
            }

            &.left {
                right: 550px;
            }

        }



    }

}
