@use '../../style/variables.scss' as v;

.header {
    background-color: v.$header-background-color;
    display: flex;
    align-items: center;
    border-bottom: v.$header_border_size solid v.$header_border_color;
}

.header_logo{
    height: v.$header-size;
}

.header_logo > img {
    height: v.$header-size;
    background-color: #fff;
}

.header_state {
    margin-left: 33%;
    display: flex;
    font-family: v.$font;
    font-size: 1.1em;

    a {

        color: #fff;
        text-decoration: none;
        
        margin-right: 30px;

        &:last-child {
            margin-right: 0px;
        }

        &:hover {
            color: rgb(224, 224, 224);
        }

        &:active {
            color: rgb(255, 255, 255);
        }

        &.active{
            position: relative;
            color: v.$header_active_color;

            &:after{
                content: '';
                position: absolute;
                top: 100%;
                left: 10%;
                width: 80%;
                height: 3px;
                border-radius: 2px;
                background-color: v.$header_active_color;
            }
        }
    }
}