$font: Helvetica, sans_serif;
// $primary_color: #333333;

$primary_color: #f1f3f5;
// $primary_color: #38618C;
$secondary_color: rgb(42, 67, 94);
// $accent_color: #ffd972;
$accent_color: #FFC766;
$contrast_color: #e9eef5;

$body_background_color: #f1f3f5;
// $body_background_color: #38618C;
// $body_background_color: #ece6f0;
// $body_background_color: linear-gradient(-30deg, rgba(62,56,98,1) 0%, rgb(255, 255, 255) 40%);
// $body_background_color: linear-gradient(15deg, #8094AB 0%, #A7C1DE 100%);
// $body_background_color: linear-gradient(15deg, rgba(42, 67, 94, 0.5) 0%, rgba(87, 164, 227, 0.5) 100%);
// $body_background_color: linear-gradient(15deg, rgba(62,56,98,0.6) 0%, rgba(0,120,162,0.6) 100%);
//#3E3862


$header_background_color: #2A435E;
$header_active_color: #FFC766;
// $header_background_color: rgb(241, 245, 248);
$header_size: 50px;
$header_border_size: 1px;
$header_border_color: rgb(255, 255, 255);
$header_total_size: calc($header_size + $header_border_size);

$footer_background_color: rgb(225, 225, 225);
$footer_total_size: 0px;

$content_title_color: #FFC766;
$content_background_color: rgb(255, 255, 255, 0);
$content_min_height: calc(100vh - $header_total_size - $footer_total_size);
$content_width: 80vw;


//Buttons
$add_button_background_color: rgb(27, 85, 166);
$add_button_background_color_hover: rgb(41, 120, 231);
$add_button_background_color_active: rgb(27, 85, 166);
$delete_button_background_color: rgb(196, 29, 29);
$delete_button_background_color_hover: rgb(255, 27, 27);
$delete_button_background_color_active: rgb(196, 29, 29);
$other_button_background_color: rgb(12, 12, 12);
$other_button_background_color_hover: rgb(42, 42, 42);
$other_button_background_color_active: rgb(12, 12, 12);

$icon_button_color: rgb(245, 245, 245);
$icon_button_height: 30px;
$icon_button_width: 30px;
$icon_button_border_radius: 4px;
$icon_button_border: 0px;
$icon_button_font_size: 20px;
$icon_button_font_weight: 200;

$floating_button_color: rgb(245, 245, 245);
$floating_button_height: 40px;
$floating_button_width: 40px;
$floating_button_border_radius: 50%;
$floating_button_border: 0px;
$floating_button_font_size: 30px;
$floating_button_font_weight: 200;

$mini_button_color: rgb(245, 245, 245);
$mini_button_height: 21px;
$mini_button_width: 21px;
$mini_button_border_radius: 50%;
$mini_button_border: 0px;
$mini_button_font_size: 20px;
$mini_button_font_weight: 200;

$text_button_color: $secondary_color;
$text_button_background_color: rgb(245, 245, 245);
$text_button_height: 40px;
$text_button_width: auto;
$text_button_border_radius: 20px;
$text_button_border: 0px;
$text_button_font_size: 1em;
$text_button_font_weight: bold;
$text_button_padding: 0 15px;


// JsonGenerator Page
$section_background_color: rgb(235, 235, 235);
$card_background_color: rgb(248, 248, 248);
$card_box_shadow: rgba(0, 0, 0, 0.65) 0px 4px 8px;
$card_box_shadow_hover: rgba(0, 0, 0, 0.7) 0px 5px 15px;
$card_box_shadow_active: rgba(0, 0, 0, 0.35) 1.95px 1.95px 2.6px;
$card_border_radius: 3px;
$card_padding: 10px;
$card_margin_right: 15px;
$card_margin_bottom: 20px;
$card_overview_height: 100px;
$card_overview_width: 180px;

// Forms
$label_color_primary: rgb(0, 0, 0);
$label_font_size_primary: 1rem;
$label_color_secondary: rgb(58, 58, 58);
$label_font_size_secondary: 0.8rem;

// JSON IHM
$input_row_size: 45px;
$input_row_gap: 10px;

// Template
$card_template_height: 260px;
$card_template_width: 210px;
$card_template_margin_right: 15px;
$card_template_margin_bottom: 35px;