@use '../../style/variables.scss' as v;

.qs_content {
    height: v.$content_min_height;
    overflow-x: auto;

    .qs_title {
        color: #2A435E;
        font-size: 2.5em;
        // font-weight: bold;
        margin: 20px 0;
        position: relative;

        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0%;
            width: 40px;
            height: 4px;
            background-color: v.$accent_color;
        }

    }
    
    .section {
        margin-top: 25px;

        .title_section {
            color: #2A435E;
            font-weight: bold;
            font-size: 1.3em;
        }

        .text {
            margin-top: 15px;
        }

        ul {
            margin: 0 15px;
        }
    }

}