@use '../../style/variables.scss' as v;

.documentation {

    display: flex;
    flex-direction: row;
    height: v.$content_min_height;

}

.menu {
    margin: 20px;
    display: flex;
    flex-direction: column;
    // background-color: #f00;
    border-right: 1px solid #ccc;
    padding-right: 20px;

    .menu_title {
        font-size: 1.5em;
        font-weight: bold;
        margin-bottom: 10px;
        margin-top: 20px;
        
        cursor: pointer;

        &:hover {
            color: #006fdf;
        }

        &.active {
            color: #006fdf;
        }
        
        &:first-child {
            margin-top: 0px;
        }

    }

    .menu_item {

        font-size: 1.2em;
        margin-top: 10px;
        margin-left: 10px;
        cursor: pointer;

        &:hover {
            color: #006fdf;
        }

        &.active {
            color: #006fdf;
        }
        
        &:first-child {
            margin-top: 0px;
        }

    }

}

.document_content {

    flex-grow: 2;
    margin: 20px;
    display: flex;
    flex-direction: column;
    overflow-x: auto;
    padding-right: 10px;

    .document_title {
        color: #2A435E;
        font-size: 2.5em;
        // font-weight: bold;
        margin-bottom: 20px;
        position: relative;

        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0%;
            width: 40px;
            height: 4px;
            background-color: v.$accent_color;
        }

    }

    .img_example_container{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 20px;
        margin-bottom: 20px;

        .img_example {

            width: 800px;
            border: 1px solid #ccc;
            padding: 10px;

        }
    }


    h1 {
        color: #00254f;
        font-size: 1.7em;
        margin-top: 20px;

        & + p {
            margin-top: 15px;
            margin-left: 15px;
        }

    }

    h2 {
        color: #002e63;
        font-size: 1.3em;
        margin-top: 20px;
        margin-left: 15px;

        & + p {
            margin-top: 10px;
            margin-left: 30px;
        }

    }

    h3 {
        color: #00499c;
        font-size: 1.1em;
        margin-top: 12px;
        margin-left: 30px;
        
        & + p {
            margin-top: 8px;
            margin-left: 45px;
        }
    }

    h4 {
        color: #016be4;
        font-size: 1.00em;
        margin-top: 10px;
        margin-left: 45px;
        
        & + p {
            margin-top: 8px;
            margin-left: 55px;
        }
    }

    p {
        color: #444;
        font-size: 1em;
        margin-top: 5px;
    }
    
    pre {
        background-color: #f5f5f5;
        padding: 10px;
        border: 1px solid #ccc;
        font-size: 0.9em;
        margin: 20px 10px;
    }

    .keyword{
        color: #00499c;
        font-weight: bold;
    }

}