@use './style/variables.scss' as v;

* {    
    margin: 0;
    padding: 0;
    font-family: v.$font;
}

body {
    background: v.$body_background_color;
    overflow: hidden;
}

.content {
    background: v.$primary_color;
    // background-color: v.$content_background_color;
    min-height: v.$content_min_height;
    width: v.$content_width;
    margin: auto;
    // overflow: scroll;

    .title {
        color: v.$accent_color;
        position: relative;
        width: fit-content;
        font-family: v.$font; 
        font-size: 2em;
        // margin: 20px 0;

        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0%;
            width: 40px;
            height: 2px;
            background-color: v.$accent_color;
        }

        // &::after{
        //     content: '';
        //     position: absolute;
        //     top: -5px;
        //     right: -12px;
        //     width: 20px;
        //     height: 20px;
        //     border-right: 2px solid v.$content_title_color;
        //     border-top: 2px solid v.$content_title_color;
        //     // background-color: v.$content_title_color;
        // }

        // &::before{
        //     content: '';
        //     position: absolute;
        //     bottom: -5px;
        //     left: -12px;
        //     width: 20px;
        //     height: 20px;
        //     border-left: 2px solid v.$content_title_color;
        //     border-bottom: 2px solid v.$content_title_color;
        //     // background-color: v.$content_title_color;
        // }
    }
    
}

.header {
    height: v.$header_size;
}


button {

    &#delete {
        background-color: v.$delete_button_background_color;

        &:hover {
            background-color: v.$delete_button_background_color_hover;
        }

        &:active {
            background-color: v.$delete_button_background_color_active;
        }
    }

    &#add {
        background-color: v.$add_button_background_color;

        &:hover {
            background-color: v.$add_button_background_color_hover;
        }

        &:active {
            background-color: v.$add_button_background_color_active;
        }
    }

    &.icon_button {
        user-select: none;
        height: v.$icon_button_height;
        width: v.$icon_button_width;
        color: v.$icon_button_color;
        border-radius: v.$icon_button_border_radius;
        border: v.$icon_button_border;
        font-size: v.$icon_button_font_size;
        font-weight: v.$icon_button_font_weight;
        transition: box-shadow 0.1s, background-color 0.1s;

        background-color: v.$other_button_background_color;

        &:hover {
            background-color: v.$other_button_background_color_hover;
        }

        &:active {
            background-color: v.$other_button_background_color_active;
        }
    }
    
    &.floating_button {
        user-select: none;
        height: v.$floating_button_height;
        width: v.$floating_button_width;
        color: v.$floating_button_color;
        border-radius: v.$floating_button_border_radius;
        border: v.$floating_button_border;
        font-size: v.$floating_button_font_size;
        font-weight: v.$floating_button_font_weight;
        box-shadow: v.$card_box_shadow;
        transition: box-shadow 0.1s, background-color 0.1s;

        &:hover {
            box-shadow: v.$card_box_shadow_hover;
        }
        
        &:active {
            box-shadow: v.$card_box_shadow_active;
        }
    }

    &.mini_button {
        user-select: none;
        height: v.$mini_button_height;
        width: v.$mini_button_width;
        color: v.$mini_button_color;
        border-radius: v.$mini_button_border_radius;
        border: v.$mini_button_border;
        font-size: v.$mini_button_font_size;
        // font-weight: v.$mini_button_font_weight;
        transition: box-shadow 0.1s, background-color 0.1s;
        // text-align: center;
        // vertical-align: 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;

        &#delete {
            
            & > span {
                position: absolute;
                top: -9px;
                left: 5px;
                font-size: 30px;
            }

        }

        &#add {
            
            & > span {
                position: absolute;
                top: -2px;
                left: 4px;
                font-size: 23px;
            }
            
        }

        &:hover {
        }
        
        &:active {
        }
    }

}

a, button {

    &.text_button {
        user-select: none;
        padding: v.$text_button_padding;
        height: v.$text_button_height;
        width: v.$text_button_width;
        color: v.$text_button_color;
        background-color: v.$text_button_background_color;
        border-radius: v.$text_button_border_radius;
        border: v.$text_button_border;
        font-size: v.$text_button_font_size;
        font-weight: v.$text_button_font_weight;
        transition: box-shadow 0.1s, background-color 0.1s;
        text-align: center;
        text-decoration: none;
        box-shadow: v.$card_box_shadow;
        transition: box-shadow 0.1s, background-color 0.1s;

        &:hover {
            box-shadow: v.$card_box_shadow_hover;
        }
        
        &:active {
            box-shadow: v.$card_box_shadow_active;
        }
    }
    
}

a.text_button {
    display: flex;
    justify-content: center;
    align-items: center;
}

.label_secondary {
    display: flex;
    flex-direction: column;
    margin-bottom: 8px;

    span {
        font-size: v.$label_font_size_secondary;
        color: v.$label_color_secondary;
    }
}